  /* Importing Poppins font */
  @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');
  * {
    box-sizing: border-box; /* Include padding and border in element's total width and height */
    margin: 0;
    padding: 0;
  }
  /* Global styles */
  body {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #FAF3E0;
    overflow-x: hidden;
    overflow-y: scroll;
  }

  /* Header styles */
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 50px;
  }

  .logo {
    font-size: 24px;
    font-weight: bold;
    color: #333;
  }

  .nav-links {
    list-style: none;
    display: flex;
    gap: 30px;
  }

  .nav-links li {
    padding: 10px;
    position: relative;
  }

  .nav-links a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
    position: relative;
  }

  .nav-links a::after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -5px; /* Positioning the underline below the text */
    width: 0;
    height: 2px;
    background-color: #000000;
    transition: width 0.3s ease, left 0.3s ease;
  }

  .nav-links a:hover::after {
    width: 100%;
    left: 0;
  }

  .login-btn {
    padding: 10px 20px;
    background-color: #00b894;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  .hero {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px;
  }

  .hero-text h1 {
    font-size: 48px;
    color: #333;
    line-height: 1.2;
    max-width: 600px;
    margin-bottom: 30px;
  }

  .categories {
    display: flex;
    gap: 15px;
    margin: 20px 0;
  }

  .category-btn {
    padding: 10px 10px 20px 10px;
    background-color: #fff;
    border: 2px solid #ddd;
    border-radius: 50px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.3s;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0px 4px 10px rgb(121, 119, 119);
  }

  .category-btn img {
    width: 75px;
    background-color: #ddd;
    border-radius: 50px;
    padding: 10px;
  }

  .category-btn p {
    margin: 0;
    font-weight: bold;
    padding-top: 30px;
  }

  .category-btn:hover {
    background-color: #00b894;
    color: #fff;
    transform: translateY(-5px);
  }

  .zip-code {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 20px;
    justify-content: center;
    margin-bottom: 1rem;
  }

  .zip-code-input {
    padding: 10px;
    border: 2px solid #ddd;
    border-radius: 5px;
    width: 300px;
  }

  .zip-code-input:focus {
    outline: none;
    box-shadow: 0px 0px 10px #000000;
    transition: 1s;
  }

  .zip-code-btn {
    padding: 10px 20px;
    background-color: #00b894;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 0rem;
  }

  .hero-image {
    position: relative;
  }

  .hero-image img {
    width: 500px;
    height: auto;
    border-radius: 50%;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    transform: rotate(0deg);
    transition: transform 1s;
  }

  .hero-image img:hover {
    transform: rotate(360deg);
  }

  .discount-badge {
    position: absolute;
    bottom: 20px;
    right: 20px;
    background-color: #00b894;
    color: #fff;
    padding: 10px 20px;
    border-radius: 50px;
    font-weight: bold;
    transform: rotate(-15deg);
  }

  .profile-container {
    display: flex;
    align-items: center;
    padding: 20px 0px 20px 0px;
    margin-left: 9rem;
  }

  .profile-img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 2px solid #ddd;
    display: flex;
  }

  .logout-btn {
    background-color: #f44336; /* Red for logout */
    color: white;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    cursor: pointer;
    display: none;
    top: -2%;
  }

  .profile-container:hover .logout-btn {
    display: flex;
    position: fixed;
    margin-top: 4.5rem;
    margin-left: -2.5rem;
  }

  /* Fetched Data Section */

  .fetched-data {
    padding: 20px;
  }

  .fetched-data h2 {
    text-align: center;
    margin-bottom: 20px;
  }

  .fetched-data-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* Four columns */
    gap: 20px;
    padding: 0 10px;
  }

  .fetched-item {
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #ffffff8e;
    padding: 10px;
    text-align: center;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .fetched-item-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 5px;
    margin-bottom: 10px;
  }

  .fetched-item-text h3 {
    margin: 10px 0;
    font-size: 16px;
  }

  .fetched-item-text p {
    margin: 0;
    font-size: 14px;
  }

  /* Responsive Design */
  @media (max-width: 1200px) {
    .fetched-data-grid {
      grid-template-columns: repeat(3, 1fr); /* Three columns on medium screens */
    }
  }

  @media (max-width: 800px) {
    .fetched-data-grid {
      grid-template-columns: repeat(2, 1fr); /* Two columns on small screens */
    }
  }

  @media (max-width: 500px) {
    .fetched-data-grid {
      grid-template-columns: 1fr; /* One column on very small screens */
    }
  }

  .footer{
    background-color: #5a5858;
    color: white;
    padding: 5px;
    text-align: center;
  }

  .footer img{
    width: 25px;
    gap: 20px;
    margin: 10px 10px 10px 10px;
    cursor: pointer;
  }

  .name{
    font-style: italic;
    padding: 10px;
  }

  .food{
    background-color: #69927e;
    color: #ffffff;
    border-top-left-radius: 5rem;
    border-top-right-radius: 5rem;
  }

  .icon i{
    color: #ffffff;
    margin: 0px 10px 0px 10px;
  }

  .icon i:hover{
    color: #000000;
    gap: 20px;
    margin: 0px 10px 0px 10px;
    cursor: pointer;
  }

  .icon{
    background-color: #00b894;
    width: 95%;
    justify-content: end;
    display: flex;
    padding:  10px 10px 5px 10px;
    border-radius: 00.2rem;
    border: 1px solid rgb(0, 0, 0);
  }

  .fetch-msg{
    color: rgb(255, 255, 255);
    margin-bottom: 20px;
    text-align: center;
    padding: 10px;
  }

  .fetch-msg img{
    width: 50%;
  }


  .fas.fa-heart.favorite {
    color: red;
  }

  .fa-thumbs-up.thumbs-up-yellow {
    color: rgb(255, 0, 0); /* Default color */
  }
  
  .view{
    background-color: #00b894;
    border: 1px solid black;
    padding: 5px 10px;
    border-radius: 0.3rem;
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;   
    cursor: pointer;
  }

  .popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.212);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
  }
  
  .popup-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgb(0, 0, 0);
    max-width: 500px;
    width: 100%;
    text-align: center;
  }
  
  .popup-content h2 {
    color: #2d6a4f; /* Deep green color for headings */
  }
  
  .popup-content p {
    color: #030303; /* Medium green color for text */
  }
  
  .no-scroll {
    overflow: hidden;
  }

  .popup-content img{
    width: 20px;
    align-items: end;
    cursor: pointer;
  }

  .cancel{
    display: flex;
    align-items: end;
    justify-content: end;
  }
/* Default styles for larger screens */
.menu-toggle {
  display: none; /* Hide the menu icon by default */
}

/* Navigation links visible on larger screens */
.nav-links {
  display: flex;
  gap: 30px;
  list-style: none;
}

.nav-links img{
  display: none;
}

@media (max-width: 768px) {

  .nav-links img{
    display: block;
    width: 100%;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 10px 0px 10px;
    background-color: #00b894;
    overflow: hidden;
    z-index: 999;
    position: fixed;
    width: 100%;
  }

  .logo {
    font-size: 20px;
    text-align: center;
    flex-grow: 1;
    order: 2; /* Position the logo in the middle */
  }

  .hero {
    flex-direction: column;
    text-align: center;
  }

  .hero-text h1 {
    font-size: 36px;
    margin-top: 20px;
  }

  .hero-image img {
    width: 100%;
    max-width: 300px;
  }

  .profile-container {
    position: relative;
    display: inline-block;
    order: 3;
    margin-right: 10px;
  }


  .google-btn{
    margin-top: 1rem;
  }

  .log-container {
    order: 3;
    padding: 10%;
    margin-bottom: 10rem;
  }
  
  .profile-img{
    background-color: #f44336;
    margin: -10px;
  }

  .category-btn {
    padding: 10px;
    width: 100%;
  }

  .zip-code-input {
    width: 100%;
  }

  .zip-code-btn {
    width: 100%;
  }


  .footer {
    font-size: 14px;
  }

  .footer p{
    padding: 10px;
  }

  .footer img {
    width: 20px;
    margin: 10 15px;
    padding-top: 1rem;
  }

  .popup-content img {
    width: 30px;
  }

  .hero-text h1 {
    font-size: 28px;
    margin-bottom: 15px;
  }

  .hero-image img {
    width: 100%;
    display: flex;
  }

  .fetched-data-grid {
    grid-template-columns: repeat(2, 1fr); /* Two columns on small screens */
  }

  .fetched-item{
    padding: 10px;
    margin-left: -0.3rem;
  }


  .category-btn {
    width: 100%;
    grid-template-columns: repeat(2, 1fr); /* Two columns on small screens */
    display: grid;
    align-items: center;
  }
  
  .category-btn p{
    padding: 0px 15px;
  }

  .category-btn img{
    width: 5rem;
  }

  .categories{
    grid-template-columns: repeat(2, 1fr); /* Two columns on small screens */
    display: grid;
    justify-content: space-evenly;
    align-items: center;
  }

  .menu-toggle {
     display: block;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: hsl(0, 0%, 0%);
    order: 1; /* Keep the menu icon on the left */
  }

  /* Hide the navigation links by default on mobile screens */
  .nav-links {
    display: none;
    flex-direction: column;
    gap: 15px;
    background-color: #00b894;
    z-index: 999;
    position: fixed;
    left: 0;
    top: 58px; 
    width: 55%; 
    min-height: 1000vh;
    position: fixed;
  }

  li{
    margin-left: 1.5rem;
    display: flex;   
    padding: 0.5rem;
    gap: 20rem;
    margin-top: 1rem;
    margin-right: 1rem;
    border-radius: 0.3rem;
    color: #fff;
  }

  /* Show the nav links when the mobile menu is open */
  .nav-mobile.open .nav-links {
    display: block;
  }

}

